.vnDetailContainer p {
    color: #f1f2fb !important;
}
/*----------------------LIVE HOVER----------------------*/
.liveOverlayContainer {
    position: relative;
}

.liveOverlay {
    z-index: 99999 !important;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
}

.liveOverlayContainer:hover .liveOverlay {
    opacity: 1;
}
/*----------------------APP NAVBAR----------------------*/
/* Add a black background color to the top navigation */
@font-face {
    font-family: "BKoodakBold";
    src: url("fonts/bkoodak/BKoodakBold.eot");
    src: url("fonts/bkoodak/BKoodakBold.woff") format("woff")
    /*url("CustomFont.otf") format("opentype"),*/
    /*url("CustomFont.svg#filename") format("svg");*/
}
@font-face {
    font-family: 'BYekan';
    src: url('fonts/BYekan/BYekan.eot?#') format('eot'),
    url('fonts/BYekan/BYekan.ttf') format('truetype'),
    url('fonts/BYekan/BYekan.woff') format('woff');
}
body {
    margin: 0;
    /*font-family:"B Nazanin", Arial, Helvetica, sans-serif;*/
    font-family:"BYekan", Arial, Helvetica, sans-serif;
    /*font-weight: 50 !important;*/
    /*font-family:"BKoodakBold", Arial, Helvetica, sans-serif;*/
    direction: rtl;
}

.topnav {
    background-color: #333;
    overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
    background-color: #ddd;
    color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
    background-color: #04AA6D;
    color: white;
}

/*------------------------------------------------------*/

.card-content-live * {
    direction: rtl;
    display: block;
    margin: auto;
    text-align: center;
    font-family: B Nazanin;
    font-size: 18px;
}

/*------------------------------------------------------*/


.imageVNItem {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
}

.middleVNItem {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.containerVNItem:hover .imageVNItem {
    opacity: 0.3;
}

.containerVNItem:hover .middleVNItem {
    opacity: 1;
}
.textVNItem {
    background-color: #04AA6D;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
}

/*------------------------------------------------------*/
.owl-prev, .owl-next {
    width: 15px;
    height: 100px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: block !important;
    border:0px solid black;
}
.owl-prev { left: -20px; }
.owl-next { right: -20px; }
.owl-prev i, .owl-next i {transform : scale(2,5); color: #ccc;}


/*------------------------------------------------------*/

.shimmerText {
    display: none;
    margin: 0 auto;
    text-align: center;
    font-family: "Quicksand", sans-serif;
    font-weight: 300;
    font-size: 32px;
    padding-top: 32px;
    color: rgba(0, 0, 0, 0.84);
}
@media screen and (min-width: 768px) {
    .shimmerText {
        display: flex;
    }
}

.shimmerMaterial-icons {
    color: rgba(0, 0, 0, 0.54);
}

.shimmerContainer {
    display: flex;
    flex-direction: column;
}

.shimmerNav {
    display: flex;
    height: 56px;
    padding: 8px;
    justify-content: space-between;
}

.shimmerNav-items {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.shimmerHeader {
    display: flex;
    flex-direction: column;
    max-width: 530px;
    width: 100%;
    background: #fff;
    box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.08);
    margin: 0 auto;
}
@media screen and (min-width: 768px) {
    .shimmerHeader {
        margin: 56px auto 0;
    }
}

.shimmerContent {
    padding: 16px 30px 24px;
}

.shimmerAnimated {
    background-color: #f3f3f3;
    background: linear-gradient(to right, #f3f3f3 5%, #eee 20%, #f3f3f3 35%);
    -webkit-animation: placeholderShimmer 2s linear 0s infinite normal forwards;
    animation: placeholderShimmer 2s linear 0s infinite normal forwards;
}

.shimmerTitle {
    position: relative;
    height: 28px;
}
.shimmerTitle:after {
    position: absolute;
    right: 0;
    content: "";
    background: #fff;
    height: 28px;
    width: 0%;
}

.shimmerPadding {
    height: 16px;
    background: #fff;
}

.shimmerParent {
    position: relative;
    height: 13px;
}
.shimmerParent:after {
    position: absolute;
    right: 0;
    content: "";
    background: #fff;
    height: 14px;
    width: 70%;
}

.shimmerFollowers {
    position: relative;
    height: 14px;
}
.shimmerFollowers:after {
    position: absolute;
    right: 0;
    content: "";
    background: #fff;
    height: 14px;
    width: 80%;
}

.shimmerDescription {
    position: relative;
    height: 14px;
}
.shimmerDescription:after {
    position: absolute;
    right: 0;
    content: "";
    background: #fff;
    height: 14px;
    width: 0%;
}

.shimmerAvatar {
    position: relative;
    display: flex;
    justify-content: flex-end;
}
.shimmerAvatar:before {
    position: absolute;
    left: 0;
    content: "";
    background: #fff;
    height: 40px;
    width: calc(100% - 40px);
}

.shimmerAvatar-image {
    width: 40px;
    height: 40px;
}

@-webkit-keyframes placeholderShimmer {
    0% {
        background-position: -500px 0;
    }
    100% {
        background-position: 500px 0;
    }
}

@keyframes placeholderShimmer {
    0% {
        background-position: -500px 0;
    }
    100% {
        background-position: 500px 0;
    }
}